html {
	font-size: 16px;
	line-height: 1.15;
	-webkit-text-size-adjust: 100%;
	height: 100%;
}

body {
	margin: 0;
	overflow: hidden;
	width: 100%;
	height: 100%;
}

#app {
	min-height: 100% !important;
	height: 100%;
}

h1 {
	font-size: 2em;
	margin: 0.67em 0;
}

hr {
	box-sizing: content-box;
	height: 0;
	overflow: visible;
}

pre {
	font-family: monospace, monospace;
	font-size: 1em;
}

a {
	background-color: transparent;
}

abbr[title] {
	border-bottom: none;
	text-decoration: underline;
	text-decoration: underline dotted;
}

b,
strong {
	font-weight: bolder;
}

code,
kbd,
samp {
	font-family: monospace, monospace;
	font-size: 1em;
}

small {
	font-size: 80%;
}

sub,
sup {
	font-size: 75%;
	line-height: 0;
	position: relative;
	vertical-align: baseline;
}

sub {
	bottom: -0.25em;
}

sup {
	top: -0.5em;
}

img {
	border-style: none;
}

button,
input,
optgroup,
select,
textarea {
	font-family: inherit;
	font-size: 100%;
	line-height: 1.15;
	margin: 0;
}

button,
input {
	overflow: visible;
}

button,
select {
	text-transform: none;
}

button,
[type="button"],
[type="reset"],
[type="submit"] {
	-webkit-appearance: button;
}

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
	border-style: none;
	padding: 0;
}

button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
	outline: 1px dotted ButtonText;
}

fieldset {
	padding: 0.35em 0.75em 0.625em;
}

legend {
	box-sizing: border-box;
	color: inherit;
	display: table;
	max-width: 100%;
	padding: 0;
	white-space: normal;
}

progress {
	vertical-align: baseline;
}

textarea {
	overflow: auto;
}

[type="checkbox"],
[type="radio"] {
	box-sizing: border-box;
	padding: 0;
}

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
	height: auto;
}

[type="search"] {
	-webkit-appearance: textfield;
	outline-offset: -2px;
}

[type="search"]::-webkit-search-decoration {
	-webkit-appearance: none;
}

::-webkit-file-upload-button {
	-webkit-appearance: button;
	font: inherit;
}

details {
	display: block;
}

summary {
	display: list-item;
}

template {
	display: none;
}

[hidden] {
	display: none;
}

@font-face {
	font-family: VT323;
	src: url("VT323.woff2");
}

* {
	box-sizing: border-box;
	text-rendering: geometricPrecision;
}

body {
	font-family: Menlo, Monaco, Lucida Console, Liberation Mono, DejaVu Sans Mono, Bitstream Vera Sans Mono, Courier New, monospace, serif;
	word-wrap: break-word;
	/*color: #585858;*/
}

figure {
	margin: 0 0 .4rem 0;
}

figcaption {
	margin-top: 0.5rem;
}

a {
	cursor: pointer;
	text-decoration: none;
	color: #fff;
	border-bottom: 1px solid #fff;
}

a:hover {
	background-color: #FF2E88;
	color: white;
}

.text-center {
	text-align: center;
}

.text-left {
	text-align: left;
}

.text-right {
	text-align: right;
}

.hidden {
	display: none;
}

.float-left {
	float: left;
}

.float-right {
	float: right;
}

.clearfix:before, .clearfix:after {
	display: table;
	content: " ";
}

.clearfix:after {
	clear: both;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	font-family: VT323, monospace;
	letter-spacing: -.02rem;
	margin-top: 0;
	margin-bottom: .8em;
}

p {
	margin-top: 0;
	letter-spacing: -.02rem;
}

hr {
	border: 0;
	height: 20px;
	margin: 10px 0;
	background-position-y: center;
	background-size: 8px 3px;
	background-repeat: repeat-x;
	background-image: linear-gradient(to right, #585858 0%, #585858 50%, transparent 50%);
}

@media (min-width: 40rem) {
	.test {
		background-color: rebeccapurple !important;
	}
}

.button,
button,
input[type='button'],
input[type='reset'],
input[type='submit'] {
	font-family: VT323, monospace;
	font-size: 20px;
	display: inline-block;
	padding: 6px 12px;
	margin-bottom: 5px;
	border-radius: 5px;
	box-shadow: 2px 2px 0 #585858;
	cursor: pointer;
	background-color: #fff;
	border: 2px solid #585858;
	border-style: outset;
}

.button:active,
button:active,
input[type='button']:active,
input[type='reset']:active,
input[type='submit']:active {
	box-shadow: 1px 1px 0 #585858;
	left: 1px;
	top: 1px;
}

.button:focus,
button:focus,
input[type='button']:focus,
input[type='reset']:focus,
input[type='submit']:focus {
	outline: none;
}

.button[disabled],
button[disabled],
input[type='button'][disabled],
input[type='reset'][disabled],
input[type='submit'][disabled] {
	cursor: no-drop;
}

.button.button-success,
button.button-success,
input[type='button'].button-success,
input[type='reset'].button-success,
input[type='submit'].button-success {
	background-color: #00ff7a;
}

.button.button-warning,
button.button-warning,
input[type='button'].button-warning,
input[type='reset'].button-warning,
input[type='submit'].button-warning {
	background-color: #ffce65;
}

.button.button-error,
button.button-error,
input[type='button'].button-error,
input[type='reset'].button-error,
input[type='submit'].button-error {
	background-color: #ff3d3d;
}

.button.button-info,
button.button-info,
input[type='button'].button-info,
input[type='reset'].button-info,
input[type='submit'].button-info {
	background-color: #67daff;
}

a[class='button'] {
	color: buttontext;
}

a[class='button']:hover {
	color: buttontext;
	background-color: #fff;
}

.container {
	max-width: 52.5rem;
	margin: 0 auto;
	padding: 0 0.75rem;
}

.container-fluid {
	margin: 0 auto;
	padding: 0 0.75rem;
}

.row {
	display: flex;
	flex-wrap: wrap;
	flex-direction: row;
	margin: -1rem 0 1rem -1rem;
}

.row > .col {
	display: block;
	padding: 1rem 0 0 1rem;
	flex: 1;
	max-width: 100%;
}

.row > .col-1 {
	padding: 1rem 0 0 1rem;
	flex: 0 0 8.33333%;
	max-width: 8.33333%;
}

.row > .col-2 {
	padding: 1rem 0 0 1rem;
	flex: 0 0 16.66667%;
	max-width: 16.66667%;
}

.row > .col-3 {
	padding: 1rem 0 0 1rem;
	flex: 0 0 25%;
	max-width: 25%;
}

.row > .col-4 {
	padding: 1rem 0 0 1rem;
	flex: 0 0 33.33333%;
	max-width: 33.33333%;
}

.row > .col-5 {
	padding: 1rem 0 0 1rem;
	flex: 0 0 41.66667%;
	max-width: 41.66667%;
}

.row > .col-6 {
	padding: 1rem 0 0 1rem;
	flex: 0 0 50%;
	max-width: 50%;
}

.row > .col-7 {
	padding: 1rem 0 0 1rem;
	flex: 0 0 58.33333%;
	max-width: 58.33333%;
}

.row > .col-8 {
	padding: 1rem 0 0 1rem;
	flex: 0 0 66.66667%;
	max-width: 66.66667%;
}

.row > .col-9 {
	padding: 1rem 0 0 1rem;
	flex: 0 0 75%;
	max-width: 75%;
}

.row > .col-10 {
	padding: 1rem 0 0 1rem;
	flex: 0 0 83.33333%;
	max-width: 83.33333%;
}

.row > .col-11 {
	padding: 1rem 0 0 1rem;
	flex: 0 0 91.66667%;
	max-width: 91.66667%;
}

.row > .col-12 {
	padding: 1rem 0 0 1rem;
	flex: 0 0 100%;
	max-width: 100%;
}

@media screen and (max-width: 768px) {
	.row.row-full {
		flex-direction: column;
	}
	.row.row-full > .col-1 {
		padding: 1rem 0 0 1rem;
		max-width: 100% !important;
	}
