	}
	.row.row-full > .col-7 {
		padding: 1rem 0 0 1rem;
		max-width: 100% !important;
	}
	.row.row-full > .col-8 {
		padding: 1rem 0 0 1rem;
		max-width: 100% !important;
	}
	.row.row-full > .col-9 {
		padding: 1rem 0 0 1rem;
		max-width: 100% !important;
	}
	.row.row-full > .col-10 {
		padding: 1rem 0 0 1rem;
		max-width: 100% !important;
	}
	.row.row-full > .col-11 {
		padding: 1rem 0 0 1rem;
		max-width: 100% !important;
	}
	.row.row-full > .col-12 {
		padding: 1rem 0 0 1rem;
		max-width: 100% !important;
	}
}

.table {
	width: 100%;
	max-width: 100%;
	background-color: transparent;
	border-spacing: 0;
	border-collapse: collapse;
	margin-bottom: 1.5rem;
}

.table td,
.table th {
	vertical-align: top;
	text-align: left;
	padding: 0.8rem 0.5rem;
	border-style: dashed;
}

.table thead th {
	font-weight: 700;
}

img {
	border: 8px outset #193fd5;
	display: block;
	height: auto;
	max-width: 100%;
}

.img-fit-cover {
	object-fit: cover;
}

.img-fit-contain {
	object-fit: contain;
}

code {
	font-size: 85%;
	position: relative;
	color: red;
}

pre {
	padding: 0;
	background: #e9e9e9;
}

pre > code {
	width: 100%;
	display: block;
	line-height: 1.5;
	overflow-x: auto;
	padding: 1rem;
	white-space: pre;
	color: inherit;
}

pre code[class*="language-"] {
	margin: 0;
	padding: 0;
}
